.footer {
  font-size: 10px;
  background-color: #022851;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 20px 0px;
}

.footer > p {
  color: white;
}


.footer-nav-options {
  list-style: none;
  display: flex;
}
.footer-nav-options > li {
  padding: 0px 5px;
  color: white;
}

.footer-nav-options > li > a {
  text-decoration: none;
  color: white;
  font-weight: bolder;
}

.footer-logo-icon {
  height: 35px;
}

.footer-disclaimer {
    max-width: 400px;
    font-style: italic;
    margin: 13px;
}

.footer-disclaimer > span {
    font-weight: bold;
}

.footer-disclaimer > a {
    color: white;
}